/**
 * ### Colors Primary
 * --selection-bg: hsl(277, 100%, 50%);
 * - Pink: hsl(322, 100%, 66%)
 * 
 * ### Colors Neutral
 * 
 * - Very Pale Cyan: hsl(193, 100%, 96%)
 * - Very Dark Cyan: hsl(192, 100%, 9%)
 * - Grayish Blue: hsl(208, 11%, 55%)
 * - white: hsl(0, 0%, 100%);
*/
@import url("https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;700&family=Poppins:wght@600&display=swap");
/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
* {
  margin: 0;
  padding: 0;
  font: inherit;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role=list],
ol[role=list] {
  list-style: none;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

html,
body {
  height: 100%;
}

/* Set core body defaults */
body {
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture,
svg {
  max-width: 100%;
  display: block;
}

ul {
  list-style-type: none;
}

table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td {
  text-align: left;
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }
  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
:root {
  --pink: 322, 100%, 66%;
  --very-pale-cyan: 193 100% 96%;
  --very-dark-cyan: 192, 100%, 9%;
  --grayish-blue: 208, 11%, 55%;
  --selection-bg: 277 100% 50%;
  --white: 0 0% 100%;
}

/**
 * ### Font family 
 */
/**
 * ### Size variables
 */
/**
 * ### Font weight
 */
::selection {
  background-color: hsl(var(--selection-bg));
  color: hsl(var(--white));
}

::-moz-selection {
  background-color: hsl(var(--selection-bg));
  color: hsl(var(--white));
}

html {
  font-size: 100%;
  font-family: "Poppins", sans-serif;
}

body {
  display: flex;
  flex-direction: column;
  background-color: hsl(var(--very-pale-cyan));
  background-image: url("../../../images/bg-hero-mobile.svg");
  background-repeat: no-repeat;
}
@media (min-width: 42.5em) {
  body {
    background-image: url("../../../images/bg-hero-desktop.svg");
    background-repeat: no-repeat;
  }
}

p:not([class]) {
  opacity: 0.7;
  max-width: 32ch;
}

/**
 * Colors
 */
.bg-white {
  background: hsl(var(--white));
}

.bg-very-dark-cyan {
  background: hsl(var(--very-dark-cyan));
}

.clr-white {
  color: hsl(var(--white));
}

/**
 * Position
 */
.relative {
  position: relative;
}

.absolute {
  position: absolute;
}

/**
 * ### With container
 */
.container {
  --max-width: 1110px;
  --container-padding: 1rem;
  width: min(var(--max-width), 100% - var(--container-padding) * 2);
  margin-inline: auto;
}

/**
 * ### Grid 
 */
.grid-columns {
  display: grid;
  gap: 1rem;
}
@media (min-width: 42.5em) {
  .grid-columns {
    grid-auto-flow: column;
    grid-auto-columns: 1fr;
  }
}

/**
 * ### Align items center 
 */
.align-itm-center {
  align-items: center;
}

/**
 * ### Align items center 
 */
.justify-cont-center {
  justify-content: center;
}

/**
 * ### Justify items center 
 */
.justify-item-center {
  justify-items: center;
}

.justify-cont-start {
  justify-content: start;
}

.justify-items-start {
  justify-items: start;
}

/**
 * Flow -Creating an upper edge for all descendants except the first.
 * inline style --> style="--flow-spacer: 1.5rem"
 */
:where(.flow :not(:first-child)) {
  margin-top: var(--flow-spacer, 1em);
}

/**
 * ### Margin Padding position
 */
.margin-bottom-700 {
  margin-block-end: 3rem;
}

/**
 * Padding top and bottom
 */
.padding-block-600 {
  padding-block: 2rem;
}

.padding-block-700 {
  padding-block: 3rem;
}

.padding-block-900 {
  padding-block: 5rem;
}

.padding-lr-400 {
  padding-inline: 1rem;
}

.padding-lr-600 {
  padding-inline: 2rem;
}

.padding-lr-800 {
  padding-inline: 4rem;
}

/**
 * ### Font Size
 */
.fs-400 {
  font-size: 1rem;
}

.fs-600 {
  font-size: 2rem;
}

.fs-700 {
  font-size: 3rem;
}

.btn {
  display: inline-flex;
  border: 0;
  padding: 1.25em 2.5em;
  text-decoration: none;
  border-radius: 100vmax;
  font-family: sans-serif;
  font-size: 1rem;
  font-weight: var(--fw-bold);
  line-height: 1;
  cursor: pointer;
  text-align: center;
  transition: background 250ms ease-in-out, transform 150ms ease;
  -webkit-appearance: none;
  -moz-appearance: none;
}

.btn:hover,
.btn:focus {
  background: #0053ba;
}

.btn:focus {
  outline: none;
  outline-offset: -4px;
}

.btn:active {
  transform: scale(0.99);
}

.header {
  padding-top: 2.625rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header-figure {
  width: 6.25rem;
}
@media (min-width: 42.5em) {
  .header-figure {
    width: 12.5rem;
  }
}

.header-logo {
  width: 100%;
}

.btn-header {
  padding: 0.9375rem 1.875rem;
  font-family: "Poppins", sans-serif;
  background: hsl(0deg, 0%, 100%);
}
.btn-header:hover {
  background: #fafafa;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  transition: all 0.1s ease;
}
.btn-header:active {
  background: #bfbfbf;
}
@media (min-width: 42.5em) {
  .btn-header {
    padding: 0.9375rem 3.75rem;
    box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
  }
}

.hero-wrapper {
  text-align: center;
  padding-bottom: 3.125rem;
}
@media (min-width: 42.5em) {
  .hero-wrapper {
    text-align: left;
    margin-bottom: 0;
  }
}

.hero-title {
  font-size: 1.875rem;
  font-weight: 700;
}
@media (min-width: 42.5em) {
  .hero-title {
    font-size: 2.375rem;
  }
}

.btn[data-type=inverted] {
  font-family: "Poppins", sans-serif;
  padding: 0.9375rem 3.75rem;
  box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
  color: hsl(var(--white));
  background: hsl(322deg, 100%, 66%);
}
.btn[data-type=inverted]:hover {
  background: #ff47bc;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  transition: all 0.1s ease;
}
.btn[data-type=inverted]:active {
  background: #d10084;
}

.card {
  align-items: center;
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
  border-radius: 1rem;
  padding: 1.25rem;
  gap: 3.75rem;
  text-align: center;
}
@media (min-width: 42.5em) {
  .card {
    padding: 3.125rem 1.25rem 3.125rem 6.25rem;
    text-align: left;
  }
}

:where(.cards-wrapper .card:not(:last-child)) {
  margin-bottom: 3.125rem;
}

.card-title {
  color: hsl(var(--very-dark-cyan));
  font-size: 1.25rem;
}
@media (min-width: 42.5em) {
  .card-title {
    font-size: 1.75rem;
  }
}

@media (min-width: 42.5em) {
  .card-dense {
    order: 1;
  }
}

.card-description {
  color: hsl(var(--grayish-blue));
  font-family: "Open Sans", sans-serif;
  font-weight: 400;
  font-size: 1.0625rem;
}

.community-container {
  display: grid;
}

.community-card {
  place-self: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
  border-radius: 1rem;
  padding: 1.5625rem 3.125rem;
  position: relative;
  top: 3.75rem;
  background-color: white;
  text-align: center;
}
@media (min-width: 20em) {
  .community-card {
    padding: 2.8125rem 1.875rem;
  }
}
@media (min-width: 29.375em) {
  .community-card {
    padding: 3.125rem 3.75rem;
  }
}
@media (min-width: 42.5em) {
  .community-card {
    padding: 3.125rem 6.25rem;
  }
}

.community-card-title {
  font-size: 1.125rem;
}
@media (min-width: 42.5em) {
  .community-card-title {
    font-size: 1.375rem;
  }
}

footer {
  padding-bottom: 1.875rem;
}

.footer-grid {
  display: grid;
  grid-auto-flow: row;
  gap: 3.125rem;
}
@media (min-width: 42.5em) {
  .footer-grid {
    grid-auto-flow: column;
    grid-template-columns: repeat(6, 1fr);
  }
}

.footer-item {
  font-family: "Open Sans", sans-serif;
  font-size: 0.875rem;
  color: hsl(var(--white));
}
.footer-item a {
  color: hsl(var(--white));
  font-weight: 400;
  text-decoration: none;
}

.footer-item-1 {
  margin-left: 3.125rem;
}
@media (min-width: 42.5em) {
  .footer-item-1 {
    grid-column: 1/3;
    margin-right: -1.25rem;
    margin-left: 3.125rem;
  }
}

.footer-item-2 {
  justify-items: start;
}
@media (min-width: 42.5em) {
  .footer-item-2 {
    justify-self: center;
    grid-column: 3/5;
  }
}

@media (min-width: 42.5em) {
  .footer-item-3 {
    grid-column: 5/7;
    justify-self: center;
  }
}

.footer-item-wrapper {
  display: flex;
  flex-direction: column;
}
.footer-item-wrapper a:hover {
  text-decoration: underline;
}

.footer-figure {
  width: 9.375rem;
}

.footer-logo {
  width: 100%;
  color: hsl(var(--white));
}

:where(.flow-footer-100 *) {
  margin-top: var(--flow-spacer, 1em);
}

:where(.flow-footer-200 *) {
  margin-top: var(--flow-spacer, 2em);
}

.footer-location,
.footer-phone,
.footer-email {
  display: flex;
  position: relative;
}

.footer-location::before {
  content: url(/images/icon-location.svg);
  position: absolute;
  left: -2.5rem;
}

.footer-phone::before {
  content: url(/images/icon-phone.svg);
  position: absolute;
  left: -2.5rem;
}

.footer-email::before {
  content: url(/images/icon-email.svg);
  position: absolute;
  left: -2.5rem;
}

.footer-item-social {
  display: grid;
  grid-auto-flow: column;
  justify-items: center;
  justify-content: center;
}
@media (min-width: 42.5em) {
  .footer-item-social {
    grid-auto-columns: 3.125rem;
    grid-auto-flow: column;
    justify-items: start;
  }
}

.footer-social-logo {
  width: 2.5rem;
  height: auto;
}

.circle-logo {
  border: 1px solid hsl(var(--white));
  display: grid;
  place-items: center;
  padding: 0.2rem 0.2rem;
  border-radius: 50%;
  aspect-ratio: 1;
  text-decoration: none;
}
.circle-logo:hover, .circle-logo:focus {
  color: hsl(var(--pink));
  border: 1px solid hsl(var(--pink));
}

.footer-copy {
  bottom: 0;
  right: 0;
  color: hsl(var(--white));
  font-size: 0.8125rem;
  font-family: "Open Sans", sans-serif;
  font-weight: 400;
}